@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,400;1,700&display=swap);
*{margin:0;box-sizing:border-box}body{font-family:"Roboto", sans-serif;background:#E1E1E1}.app-posts{display:flex;flex-direction:column;align-items:center}

.create-post{width:500px;height:inherit;padding:15px;background:#fff;box-shadow:0 0 15px rgba(0,0,0,0.1);border-radius:10px;margin-top:15px}.create-post h3{margin-bottom:10px}.create-post input:nth-of-type(1){margin-bottom:10px}.create-post input{padding:10px;outline:none;border-radius:5px;border:none;background:#E1E1E1;width:100%}@media (max-width: 576px){.create-post{width:90%}}

.header{position:-webkit-sticky;position:sticky;top:0;z-index:999;display:flex;align-items:center;width:100%;background:#fff;height:60px;box-shadow:0 0 15px rgba(0,0,0,0.1)}.header h1{margin-left:25px}.header .header-search{margin-left:50px;display:flex;align-items:center;background:#E1E1E1;border-radius:5px;padding:5px}.header .header-search input{width:100%;border:none;outline:none;background:none}@media (max-width: 576px){.header{flex-direction:column;height:inherit;align-items:center;padding-bottom:10px}.header h1{margin:0}.header .header-search{margin:0;width:60%}}

.post{width:500px;height:inherit;background:#fff;border-radius:10px;box-shadow:0 0 15px rgba(0,0,0,0.1);padding:15px;margin-top:15px}.post .post-top{display:flex}.post .post-top .post-stat{margin-left:10px;width:100%}.post .post-top .post-stat h5{color:#ACACAC}.post p{margin-top:10px}.post .post-delete{color:#c53333}@media (max-width: 576px){.post{width:90%}}

